@import "../../assets/styles/app";

.logo {
  position: absolute;
  top: 90px;
  left: 80px;
  display: block;
  width: 4px;
  height: 4px;
  background: red;

  @include tablet {
    top: 180px;
    left: 215px;
  }

  .inner {
    position: relative;


    .link {
      display: block;
      position: absolute;
      left: 50%;
      top: calc(50% - 15px);
      transform: translate(-50%, -50%);

      @include tablet {
        top: calc(50% - 25px);
      }

      img {
        display: block;
        width: 118px;
        height: auto;
        transition: 0.3s;

        @include tablet {
          width: 270px;
        }

        &:hover {
          transform: scale(0.9);
        }
      }
    }
  }
}
